import { Input, SubmitBtn, Title, FormContainer } from './LoginPage.styled';

import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { logIn } from '../../redux/authSlice';

import userBase from '../../Storage/login.json';
import { notifyError, notifySuccess } from 'module/Toaster/Toaster';

const userLoggining = (name, password) => {
  const isNameInBase = Object.keys(userBase).includes(name);
  const passwordVerified = userBase[name]?.password === password;
  const role = userBase[name]?.role;

  return new Promise((resolve, reject) => {
    if (isNameInBase && passwordVerified) {
      resolve({ name, role });
    } else {
      reject();
    }
  });
};

export const LoginPage = () => {
  const dispatch = useDispatch();
  let navigate = useNavigate();

  const handleFormSubmit = e => {
    e.preventDefault();
    const form = e.currentTarget;
    const name = form.elements.name.value;
    const password = form.elements.password.value;
    userLoggining(name, password)
      .then(data => {
        dispatch(logIn(data));
        notifySuccess(name, data.role);
        navigate('/home', { replace: true });
      })
      .catch(notifyError);
    form.reset();
  };

  return (
    <FormContainer onSubmit={handleFormSubmit}>
      <Title>–î–ª—è –ø—Ä–æ–¥–æ–≤–∂–µ–Ω–Ω—è –Ω–µ–æ–±—Ö—ñ–¥–Ω–æ –∞–≤—Ç–æ—Ä–∏–∑—É–≤–∞—Ç–∏—Å—è:</Title>
      <Input
        type="text"
        name="name"
        placeholder="–í–≤–µ–¥—ñ—Ç—å –ª–æ–≥—ñ–Ω"
        required
        maxlength="12"
      />
      <Input
        type="password"
        name="password"
        placeholder="–ü–∞—Ä–æ–ª—å"
        required
        minlength="4"
        maxlength="8"
        size="8"
      />
      <SubmitBtn type="submit">–£–≤—ñ–π—Ç–∏</SubmitBtn>
    </FormContainer>
  );
};
